import React from "react";
import Game from "./Game"



export default function App(){


  const [initgame,setInitgame]=React.useState(false)
  function initGame(){
    setInitgame(true)
  }

  return (
    <main>

      {initgame?


        <Game />
      
      : 
      
      
      <div className="start">
        <h1>Quizzical</h1>
        <h4>Some description if needed</h4>
        <button className="purple-btn font-big"  onClick={initGame}>Start quiz</button>


      </div>}
      
    </main>

  )

}