import React from "react";

export default function Question(props){


   const options=props.options
   
    return(
        <div className="question">
            <h2>{props.quest}</h2>


            <div className="options">
                {options.map((option, index) => (
                    <div 
                    key={index} 
                    className="option" 
                    style={{

                         backgroundColor: !props.results? option===props.selectedAnswer && !props.results ? "#D6DBF5" : "transparent"
                          : option===props.correct_answer? "#94D7A2" 
                          : option===props.selectedAnswer && option!==props.correct_answer? "#F8BCBC" : "transparent",


                        borderColor: !props.results? option===props.selectedAnswer && !props.results ? "transparent" : "#293264"
                            : option===props.correct_answer? "transparent" 
                            : option===props.selectedAnswer && option!==props.correct_answer? "transparent" : "#a3abcd",
                        
                        color:!props.results?  "#293264"
                        : option===props.correct_answer? "#293264" :"#a3abcd"

                        
                        
                        
                        
                    }}
                    
                    onClick={() => props.checkAnswer(props.id, option)}
                    >
                    {option}
                    </div>
  ))}
</div>

        </div>
    )

}