import React from "react";
import Question from "./Question";
import {decode} from 'html-entities';
import { nanoid } from 'nanoid'


export default function Game(){


    const [gameover,setGameover]=React.useState(false)
    const[questions,setQuestions]=React.useState([])
    const [results,setResults]=React.useState(false)
    const[score,setScore]=React.useState(0)

    React.useEffect(
       ()=>{

        fetch("https://opentdb.com/api.php?amount=5&type=multiple").then(res=>{
            return res.json()
        }).then(data=>{
            
           const arr=data.results;
           setQuestions(()=>{
            
            return arr.map((item)=>{

                const options=[item.correct_answer,...item.incorrect_answers ]
               
                    for (let i=0;i<4;i++){
                        const j =Math.floor(Math.random()*4)
                        const temp= options[i]
                        options[i]=options[j]
                        options[j]=temp
                    }

                const k={
                    quest:decode(item.question),
                    id:nanoid(),
                    isCorrect: false,
                    incorrect_answers:decode(item.incorrect_answers),
                    answer:decode(item.correct_answer),
                    options:options,
                    selectedAnswer:""
                }
                return k

            })


           })

       } ).catch(()=>console.error("hello"))
    
       setResults(false)
      
    
    
    },[gameover]
    )

   function checkAnswer(id,selectedAnswer){

        const arr=questions.map((item)=>{
            if(item.id===id){
                if(item.answer===selectedAnswer){
                    
                    return {
                        ...item,
                        isCorrect:true,
                        selectedAnswer:selectedAnswer

                    }
                }
                else{
                    
                    return {
                        ...item,
                        isCorrect:false,
                        selectedAnswer:selectedAnswer
                    }
                }
            }
            else{
                
                return item
            
            }

            

   })

    setQuestions(arr)
    
}


   function showResults(){

        let final=0

        questions.forEach(question=>{
            if(question.isCorrect===true){
                final+=1
            }
        })
        

        setScore(final)

        setResults(true)


    }
    function restart(){
        setGameover(!gameover)
    }


    return (
        <div className="game">
            {questions.length > 0 ? (
                questions.map((question) => (
                    <Question key={question.id} id={question.id} 
                    quest={question.quest}  incorrect_answers={question.incorrect_answers} 
                    correct_answer={question.answer} checkAnswer={checkAnswer} options={question.options} 
                    selectedAnswer={question.selectedAnswer}
                    results={results}

                    
                    />
                ))
            ) : (
                <p className="options">Loading questions...</p>
            )}
            {
                !results? 
                <div className="bottom-stats">
                <button className="purple-btn" onClick={showResults} >Check answers</button>
            </div>
            :
            <div className="final">

                <h2> You scored {score}/5 correct answers </h2>
                <button className="purple-btn font-big" onClick={restart}  >play again</button>
            </div>
            
            
            }
        </div>
    );
}